.single-product #content {
	border-bottom: 1px solid #dee2e6;
}

.single-product .product_title {
	text-transform: uppercase;
}
.single-product #main > .product {
	margin-top: 1.7rem;
	font-size: 0.8em;
}
.single-product #content #main > .product .summary,
.single-product #content #main > .product .product-disclaimer {
	margin-left: 1em;
	margin-right: 1em;
	width: auto;
}
.single-product #main > .product .summary {
	padding: 0;
}
.single-product #main > .product .summary .first-payment-date {
	font-size: 1.5em;
}
.single-product #main > .product .product_title {
	font-size: 1.8em;
	margin-bottom: 0;
}
.single-product #main > .product .price {
	margin: 0.5em 0;
}
.single-product #main > .product .woocommerce-product-details__short-description {
	margin-top: 0.8em;
}

.single-product .woocommerce-product-gallery__image {
	overflow: visible !important;
    width: calc(100% - 18px);
    left: 9px;
}
.single-product .woocommerce-product-gallery__image .wp-post-image {
	width: 100%;
}
.single-product .woocommerce-product-gallery__image .zoomImg {
	visibility: hidden;
}
.single-product .woocommerce-product-gallery__image:hover {
	overflow: hidden !important;
}
.single-product .woocommerce-product-gallery__image:hover .zoomImg {
	visibility: visible;
}
/* make up for 18px border + slight spacing */
.single-product #main > .product > .woocommerce-product-gallery {
	margin-top: 22px;
}
.single-product .woocommerce-product-gallery__image::before {
	top: -18px;
	right: -18px;
	bottom: -18px;
	left: -18px;
	border-width: 17px;
	border-color: white;
	background-color: currentColor;
}
.single-product .woocommerce-product-gallery__image::after {
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	border-width: 16px;
	border-color: white;
}

.variations_form table.variations {
	margin-bottom: 2.8em;
}
.variations_form td {
	border-style: none;
	vertical-align: middle;
	word-break: keep-all;
	white-space: nowrap;
}
.variations_form td.label {
	padding: 0;
}
.variations_form td.label label {
	white-space: nowrap;
}
.variations_form td.value {
	position: relative;
	text-align: right;
	padding: 0;
}
.variations_form td.value select {
	width: 100%;
	margin-right: 0;
}
.variations_form td.value .reset_variations {
	position: absolute;
	bottom: -1.5em;
	right: 0;
}
.woocommerce-variation-availability .stock {
	margin: 0 0 0.5em 0;
}
.variations_form .single_variation_wrap {
	font-size: 0.8em;
}
.variations_form .single_variation_wrap .btn {
	letter-spacing: 1px;
	padding-top: 0.6em;
}

.single-product #main .entry-summary {
	margin-bottom: 1.2rem;
}
.theme--esters #main .product-sublisting.products {
	clear: both;
	margin-bottom: 1.2rem;
}
.theme--esters #main .product-sublisting.products h2.star-heading {
	font-size: 1.3em;
	letter-spacing: 2px;
}
.theme--esters #main .product-sublisting.products .product {
	background-color: white;
	padding: 3%;
	margin-bottom: 3%;
}

.product-disclaimer {
	clear: both;
	margin-bottom: 3rem;
	line-height: 1.4;
}

@media (min-width: 768px) {
	.theme--esters #main .product-sublisting.products h2.star-heading {
		font-size: 1.5em;
		letter-spacing: 3px;
	}
	.single-product #content #main > .product .summary,
	.single-product #content #main > .product .product-disclaimer {
		float: right;
		padding: 1rem;
		margin-bottom: 1rem;
		width: 48%;
	}
}
